import React, { useState, useEffect } from 'react'
import { BankInfo } from './BankInfo'
import { NotFound } from './NotFound'
import { SCREENS } from '../utils/constants'

export const Screens = props => {

  const { store: { screen } } = props

  const getScreen = (screen) => {
    switch(screen) {
      case SCREENS.BANK:
        return <BankInfo {...props} />
      case SCREENS.NOT_FOUND:
        return <NotFound />
      default:
        return <NotFound />
    }
  }

  return(
    <React.Fragment>
      {getScreen(screen)}
    </React.Fragment>
  )
}


export const DynamicContainer = props => {

  return (
    <React.Fragment>
      <Screens {...props} />
    </React.Fragment>
  )
}