import React from 'react'
import { DynamicContainer } from './DynamicContainer'
import { Loader } from './Loader'



export const Content = props => {
  const { store } = props
  return (
    <div id="wrapper" className="container">
        <div id="content">
          {store.loading
            ? <Loader />
            : <DynamicContainer {...props} />
          }
      </div>

    </div>
  )
}