import React, {Fragment} from 'react'
import { Rating } from './../Rating'
import { translate } from './../../utils/translations'


export const AlphaBank = () => {

  return (
    <Fragment>
      <div className="head">
        <div className="name">
          <img src="/img/logo_alpha.svg" width="170px"/>
        </div>
        <Rating score="8.7" from="894" />
      </div>
      <div className="about">
        <div className="row">
          <div className="t_left">{translate('country')}</div>
          <div className="t_right">Romania</div>
        </div>
        <div className="row">
          <div className="t_left">{translate('cities')}</div>
          <div className="t_right">Bucuresti, Cluj-Napoca</div>
        </div>
        <div className="row">
          <div className="t_left">{translate('type')}</div>
          <div className="t_right">Private</div>
        </div>
        <div className="row">
          <div className="t_left">{translate('founded')}</div>
          <div className="t_right">1994 - Banca București S.A.</div>
        </div>
        <div className="row">
          <div className="t_left">Products</div>
          <div className="t_right">Commercial banking, Investment banking, Private banking, <b>Internet banking</b></div>
        </div>
        <div className="row">
          <div className="t_left">{translate('revenue')}</div>
          <div className="t_right">1.52 billion RON</div>
        </div>
        <div className="row">
          <div className="t_left">Net Income</div>
          <div className="t_right">269.5 million RON</div>
        </div>
        <div className="row">
          <div className="t_left">Deposits</div>
          <div className="t_right">67.5 million RON</div>
        </div>
        <div className="row">
          <div className="t_left">Total assets</div>
          <div className="t_right">34.6 billion RON</div>
        </div>
        <div className="row" style={{ marginTop: '20px' }}>
          <div className="t_left">Email</div>
          <div className="t_right">info@alphabank.ro</div>
        </div>
        <div className="row">
          <div className="t_left">Phone</div>
          <div className="t_right">+40 8008 25742 </div>
        </div>
        <div className="row">
          <div className="t_left">Website</div>
          <div className="t_right"><a href="https://www.alphabank.ro/">www.alphabank.ro</a></div>
        </div>

        <div style={{ marginTop: '40px' }}>
          <img src="https://www.worldbank.org.ro/images/alpha-bank-romania.jpg" width="100%" alt="alpha bank" />
        </div>

        <div style={{ marginTop: '50px'}}>
          <p>
            Alpha Bank România și-a început activitatea în România în anul 1994 și este sucursala băncii Alpha Bank din Grecia. Banca oferă o gamă largă de servicii și produse atât pentru segmentul de piață dedicat persoanelor fizice cât și pentru sectorul corporate, printr-o rețea de sucursale situate în București și alte orașe mari din țară. Alpha Bank România deține 12,5% din acțiunile celei de a doua cea mai puternică instituții bancare din Republica Moldova, Victoriabank. În septembrie 2007, activele băncii erau de 3,7 milarde euro, având o cotă de 5,8% din piața românească.          </p>
        </div>

        <div className="summary" >
          <p>
            Alpha Bank SA, societate administrata in sistem dualist, cu sediul in Romania, Bucuresti, Sector 1, Bd. Expozitiei Nr.1F, Cod Postal 012101, inregistrata in Registrul Comertului sub nr.J40/7706/1991, in Registrul Bancar sub nr.RB-PJR-40-011/18.02.1999 si inregistrata la ANSPDCP cu notificarea nr.10964, cod unic de inregistrare 361536, atribut fiscal RO, capital social subscris si varsat 455.219.478,30 RON.
            <br />
            <b>Cod BIC: BUCUROBU</b>
          </p>
        </div>

      </div>
    </Fragment>
    
  )
}


