import React, {Fragment} from 'react'
import { Rating } from './../Rating'
import { translate } from './../../utils/translations'


export const UnicreditBank = () => {

  return (
    <Fragment>
      <div className="head">
        <div className="name">
          <img src="/img/logo_unicredit.png" />
        </div>
        <Rating score="8.7" from="894" />
      </div>
      <div className="about">
        <div className="row">
          <div className="t_left">{translate('country')}</div>
          <div className="t_right">Romania</div>
        </div>
        <div className="row">
          <div className="t_left">{translate('cities')}</div>
          <div className="t_right">Bucuresti, Cluj-Napoca</div>
        </div>
        <div className="row">
          <div className="t_left">{translate('type')}</div>
          <div className="t_right">Private</div>
        </div>
        <div className="row">
          <div className="t_left">{translate('founded')}</div>
          <div className="t_right">1991 - Tiriac Bank</div>
        </div>
        <div className="row">
          <div className="t_left">Products</div>
          <div className="t_right">Commercial banking, Investment banking, Private banking, <b>Internet banking</b></div>
        </div>
        <div className="row">
          <div className="t_left">{translate('revenue')}</div>
          <div className="t_right">1.52 billion RON</div>
        </div>
        <div className="row">
          <div className="t_left">Net Income</div>
          <div className="t_right">269.5 million RON</div>
        </div>
        <div className="row">
          <div className="t_left">Deposits</div>
          <div className="t_right">67.5 million RON</div>
        </div>
        <div className="row">
          <div className="t_left">Total assets</div>
          <div className="t_right">34.6 billion RON</div>
        </div>
        <div className="row" style={{ marginTop: '20px' }}>
          <div className="t_left">Email</div>
          <div className="t_right">infocenter@unicredit.ro</div>
        </div>
        <div className="row">
          <div className="t_left">Phone</div>
          <div className="t_right">+40 21 200 2020</div>
        </div>
        <div className="row">
          <div className="t_left">Website</div>
          <div className="t_right"><a href="www.unicredit.ro">www.unicredit.ro</a></div>
        </div>

        <div style={{ marginTop: '50px'}}>
          <p>
            UniCredit Bank este parte a Grupului UniCredit.
            In Romania, UniCredit Bank este una dintre principalele institutii financiare, oferind servicii si produse de inalta calitate pentru toate categoriile de clienti. La finalul lunii Decembrie 2018, banca avea o retea nationala de 138 de sucursale si 3.337 de angajati. UniCredit Bank isi propune sa mentina permanent clientul in centrul activitatilor sale, sa fie un partener cu care se lucreaza usor si sa fie o parte foarte activa a comunitatilor in care isi desfasoara activitatea.
            Grupul raspunde unei game variate de nevoi a clientilor sai printr-o retea de parteneri specializati: UniCredit Leasing Corporation, UniCredit Consumer Financing, UniCredit Services, UniCredit Insurance Broker, Amundi Asset Management.
          </p>
        </div>

        <div className="summary" >
          <p>
            UniCredit Bank SA, societate administrata in sistem dualist, cu sediul in Romania, Bucuresti, Sector 1, Bd. Expozitiei Nr.1F, Cod Postal 012101, inregistrata in Registrul Comertului sub nr.J40/7706/1991, in Registrul Bancar sub nr.RB-PJR-40-011/18.02.1999 si inregistrata la ANSPDCP cu notificarea nr.10964, cod unic de inregistrare 361536, atribut fiscal RO, capital social subscris si varsat 455.219.478,30 RON.
            <br />
            <b>Cod BIC: BACXROBU</b>
          </p>
        </div>

      </div>
    </Fragment>
    
  )
}


