import React from 'react'
import { UnicreditBank } from './banks/UnicreditBank'
import { AlphaBank } from './banks/AlphaBank'
import { config } from '../config/config'
import { BANKS } from '../utils/constants'


const getBank = () => {
  switch (config.bank) {
    case BANKS.UNICREDIT:
      return UnicreditBank
    case BANKS.ALPHA:
      return AlphaBank
  }

}


export const BankInfo = () => {

  const Bank = getBank()

  return (
    <div id="bank-info" className={config.bank}>
      <Bank />
    </div>
    
  )
}


