import React, { useState } from 'react'
import { queryParams } from '../utils/utils'
import { translate } from '../utils/translations'

export const Search = () => {

  const [ val, setVal ] = useState(queryParams.bank)

  return (
    <div id="search">
        <form method="GET" action="/">
          <div className="container">
            <input type="text" name="bank" value={val} onChange={ e => { setVal(e.target.value) }} placeholder={translate('search_placeholder')}/>
            <button>{translate('search')}</button>
          </div>
        </form>
    </div>
    
  )
}


