import React from 'react'
import LoaderSpinner from 'react-loader-spinner'
export const Loader = props => {

  return (
    <div className="spinner-container">
      <div>
          <LoaderSpinner
            type="TailSpin"
            color="#464646"
            height={50}
            width={50}
            //timeout={3000}

          />
        <div style={{marginTop: 30}}>
          Searching...
        </div>
      </div>
    </div>
  )
}