import React from 'react'
import en from '../media/gb.png'
import ro from '../media/ro.png'
import { LANG } from '../utils/constants'
import { storage } from '../utils/storage'

const changeLang = lang => function(e) {
  e.preventDefault()
  storage.lang = lang
  window.location.reload()
}

export const Header = props => (
  <div id="header">
    <div className="container">
      <div id="logo" className="newLogo">
        <a href="#">
          <img src="/img/logo.png" alt="About Banking" style={{ height: '54px', marginTop: '6px' }} />
        </a>
      </div>
      <div id="lang" className="lang">
        <div>
          <a className="en" href="/?lang=en" onClick={changeLang(LANG.EN)}>
            <img src={en} />
            EN
          </a>
        </div>
        <div>
          <a className="ro" href="/?lang=ro" onClick={changeLang(LANG.RO)}>
            <img src={ro} />
            RO
          </a>
        </div>
      </div>
    </div>
  </div>
)